"use client";

import { useRef, useState, useCallback } from "react";
import { useGSAP } from "@gsap/react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { LABIA_TYPES } from "@/config/labiaplastyPage";

gsap.registerPlugin(ScrollTrigger);

/** 2번 이미지 모사: 6유형별 SVG path (동일 구조 M Q Q — 모핑용) */
const TYPE_PATHS: Record<
  string,
  { left: string; right: string; fill?: string; stroke?: string }
> = {
  normal: {
    left: "M 80 48 Q 62 62 64 88 Q 62 106 80 102",
    right: "M 80 48 Q 98 62 96 88 Q 98 106 80 102",
  },
  "clitoral-hypertrophy": {
    left: "M 80 28 Q 54 50 58 90 Q 54 128 80 118",
    right: "M 80 28 Q 106 50 102 90 Q 106 128 80 118",
  },
  asymmetry: {
    left: "M 80 30 Q 48 55 52 92 Q 50 128 80 115",
    right: "M 80 52 Q 98 65 96 88 Q 98 106 80 102",
  },
  "perineal-gaping": {
    left: "M 80 32 Q 52 60 56 95 Q 52 122 80 118",
    right: "M 80 32 Q 108 60 104 95 Q 108 122 80 118",
  },
  "posterior-hypertrophy": {
    left: "M 80 45 Q 58 75 60 108 Q 58 132 80 122",
    right: "M 80 45 Q 102 75 100 108 Q 102 132 80 122",
  },
  "hypertrophy-pigmentation": {
    left: "M 80 26 Q 52 52 56 94 Q 52 130 80 120",
    right: "M 80 26 Q 108 52 104 94 Q 108 130 80 120",
    fill: "#6b4545",
    stroke: "#5a3a3a",
  },
};

const OUTER_ELLIPSE = "M 80 18 C 128 18 148 92 148 108 C 148 118 118 128 80 128 C 42 128 12 118 12 108 C 12 92 32 18 80 18 Z";

/** 단일 도식 셀 — 배경(피부톤) + 외곽 + 좌우 path */
function TypeDiagramSvg({
  typeId,
  leftPath,
  rightPath,
  fill,
  stroke,
  className = "",
}: {
  typeId: string;
  leftPath: string;
  rightPath: string;
  fill?: string;
  stroke?: string;
  className?: string;
}) {
  const defaultStroke = "var(--hip-accent)";
  const defaultFill = "#d4a89a";
  return (
    <svg
      className={className}
      viewBox="0 0 160 140"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      aria-hidden
    >
      <rect width="160" height="140" fill="#ebe5e0" rx="8" />
      <path d={OUTER_ELLIPSE} stroke="#e0d8d2" strokeWidth="1" fill="#f2ebe6" />
      <path
        d={leftPath}
        fill={fill ?? defaultFill}
        stroke={stroke ?? defaultStroke}
        strokeWidth="1.2"
        strokeLinejoin="round"
      />
      <path
        d={rightPath}
        fill={fill ?? defaultFill}
        stroke={stroke ?? defaultStroke}
        strokeWidth="1.2"
        strokeLinejoin="round"
      />
    </svg>
  );
}

/**
 * 소음순유형 도식화 — 2번 이미지 모사 6유형 2x3 그리드 + 인터랙션: 유형 클릭 시 해당 형태 → 정상으로 모핑
 */
export function LabiaTypeDiagramBlock() {
  const sectionRef = useRef<HTMLElement>(null);
  const cardsRef = useRef<(HTMLElement | null)[]>([]);
  const stageLeftRef = useRef<SVGPathElement>(null);
  const stageRightRef = useRef<SVGPathElement>(null);
  const stageWrapRef = useRef<HTMLDivElement>(null);
  const [animatingId, setAnimatingId] = useState<string | null>(null);
  const [stageTypeId, setStageTypeId] = useState<string | null>(null);

  const runMorphToNormal = useCallback(() => {
    if (!stageLeftRef.current || !stageRightRef.current) return;
    const normal = TYPE_PATHS.normal;
    gsap.to(stageLeftRef.current, {
      attr: { d: normal.left },
      duration: 1,
      ease: "power2.inOut",
    });
    gsap.to(stageRightRef.current, {
      attr: { d: normal.right },
      duration: 1,
      ease: "power2.inOut",
      onComplete: () => {
        setAnimatingId(null);
        setStageTypeId("normal");
      },
    });
  }, []);

  const handleTypeClick = useCallback(
    (typeId: string) => {
      if (animatingId) return;
      const paths = TYPE_PATHS[typeId];
      if (!paths || !stageLeftRef.current || !stageRightRef.current) return;
      if (typeId === "normal") {
        setStageTypeId("normal");
        return;
      }
      setAnimatingId(typeId);
      setStageTypeId(typeId);
      gsap.set(stageLeftRef.current, { attr: { d: paths.left } });
      gsap.set(stageRightRef.current, { attr: { d: paths.right } });
      if (stageWrapRef.current) {
        gsap.fromTo(
          stageWrapRef.current,
          { opacity: 0.7, scale: 0.98 },
          { opacity: 1, scale: 1, duration: 0.3, ease: "power2.out" }
        );
      }
      gsap.delayedCall(0.35, runMorphToNormal);
    },
    [animatingId, runMorphToNormal]
  );

  useGSAP(
    () => {
      cardsRef.current.forEach((el, i) => {
        if (!el) return;
        gsap.fromTo(
          el,
          { opacity: 0, y: 24 },
          {
            opacity: 1,
            y: 0,
            duration: 0.5,
            delay: i * 0.06,
            ease: "power3.out",
            scrollTrigger: {
              trigger: sectionRef.current,
              start: "top 78%",
              toggleActions: "play none none reverse",
            },
          }
        );
      });
    },
    { scope: sectionRef, dependencies: [] }
  );

  return (
    <section
      id="labia-types"
      ref={sectionRef}
      className="relative overflow-hidden border-y border-[#e8e8e8] bg-[var(--hip-bg)] px-6 py-20 lg:px-[var(--pad-global)] lg:py-28"
    >
      <div className="absolute left-0 top-0 bottom-0 w-[3px] bg-gradient-to-b from-transparent via-[var(--hip-accent)] to-transparent opacity-40" aria-hidden />
      <div className="relative mx-auto max-w-[960px]">
        <p className="mb-2 text-center font-[family-name:var(--font-display)] text-[11px] tracking-[0.35em] uppercase text-[var(--hip-accent-dark)]">
          TYPES
        </p>
        <h3 className="mb-4 text-center font-[family-name:var(--font-heading-kr)] text-[clamp(22px,2.8vw,28px)] tracking-[0.06em] text-[#111]">
          소음순유형 도식화
        </h3>
        <p className="mb-6 text-center text-[14px] text-[#666]">
          유형을 클릭하면 <strong className="text-[var(--hip-accent-dark)]">정상(수술 후)으로 정리되는 과정</strong>을 보실 수 있습니다.
        </p>

        {/* 인터랙션 스테이지: 선택 유형 → 정상 모핑 */}
        <div
          ref={stageWrapRef}
          className="mb-12 flex flex-col items-center rounded-2xl border border-[#e8e8e8] bg-white p-6 shadow-sm"
        >
          <p className="mb-3 text-center font-[family-name:var(--font-display)] text-[11px] tracking-[0.2em] text-[#888]">
            {stageTypeId === "normal"
              ? "정상 (수술 후 모습)"
              : animatingId
                ? "정리되는 과정"
                : "유형을 선택하세요"}
          </p>
          <svg
            className="h-[200px] w-[180px] sm:h-[240px] sm:w-[220px]"
            viewBox="0 0 160 140"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
            aria-hidden
          >
            <rect width="160" height="140" fill="#f8f6f4" rx="8" />
            <path d={OUTER_ELLIPSE} stroke="#e8e2dc" strokeWidth="1" fill="#f5efe9" />
            <path
              ref={stageLeftRef}
              d={TYPE_PATHS.normal.left}
              fill="#d4a89a"
              stroke="var(--hip-accent)"
              strokeWidth="1.5"
              strokeLinejoin="round"
            />
            <path
              ref={stageRightRef}
              d={TYPE_PATHS.normal.right}
              fill="#d4a89a"
              stroke="var(--hip-accent)"
              strokeWidth="1.5"
              strokeLinejoin="round"
            />
          </svg>
        </div>

        {/* 2x3 그리드 — 2번 이미지 모사, 각 셀 클릭 시 위 스테이지에서 해당 유형 → 정상 모핑 */}
        <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 lg:grid-cols-3">
          {LABIA_TYPES.map((t, i) => {
            const paths = TYPE_PATHS[t.id];
            const isNormal = t.id === "normal";
            return (
              <article
                key={t.id}
                ref={(el) => { cardsRef.current[i] = el; }}
                role="button"
                tabIndex={0}
                onClick={() => handleTypeClick(t.id)}
                onKeyDown={(e) => e.key === "Enter" && handleTypeClick(t.id)}
                className="group flex cursor-pointer flex-col rounded-xl border border-[#e8e8e8] bg-[#faf9f8] p-5 transition-all duration-300 hover:border-[var(--hip-accent)]/50 hover:bg-white hover:shadow-lg focus:outline-none focus:ring-2 focus:ring-[var(--hip-accent)]/40"
              >
                <div className="mb-4 overflow-hidden rounded-lg bg-[#ebe5e0]">
                  {paths ? (
                    <TypeDiagramSvg
                      typeId={t.id}
                      leftPath={paths.left}
                      rightPath={paths.right}
                      fill={paths.fill}
                      stroke={paths.stroke}
                      className="aspect-[4/3] w-full object-cover"
                    />
                  ) : (
                    <div className="flex aspect-[4/3] items-center justify-center text-[11px] text-[#999]">
                      {t.id}
                    </div>
                  )}
                </div>
                <h4 className="font-[family-name:var(--font-heading-kr)] text-[15px] font-medium leading-snug tracking-[0.02em] text-[#111]">
                  {t.label}
                </h4>
                <p className="mt-2 text-[13px] leading-relaxed text-[#666]">
                  {t.desc}
                </p>
                {!isNormal && (
                  <span className="mt-3 inline-block text-[11px] tracking-[0.15em] text-[var(--hip-accent-dark)] opacity-0 transition-opacity group-hover:opacity-100">
                    클릭 → 정상으로 정리되는 과정 보기
                  </span>
                )}
              </article>
            );
          })}
        </div>
      </div>
    </section>
  );
}
